<template>
  <div class="text-center">
    <!--@input="pausarSonido()"-->
    <v-bottom-sheet dark :value="reproductor" inset>
      <v-card tile>
        <v-progress-linear
          :value="tiempoActual"
          color="#5f5f5f"
          class="my-0"
          height="3"
        ></v-progress-linear>

        <v-list color="rgb(170, 0, 0)">
          <v-list-item>
            <v-list-item-content>
              <v-list-item-title>{{ nombreCancion }}</v-list-item-title>
              <v-list-item-subtitle>{{ artistaCancion }}</v-list-item-subtitle>
            </v-list-item-content>

            <v-spacer></v-spacer>
            {{ tiempoActualMinutos }}/{{ minutos }}

            <v-menu top offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon v-bind="attrs" v-on="on">
                  <v-icon v-show="volumen > 50">mdi-volume-high</v-icon>
                  <v-icon v-show="volumen <= 50 && volumen > 0">
                    mdi-volume-medium
                  </v-icon>
                  <v-icon v-show="volumen == 0">mdi-volume-off</v-icon>
                </v-btn>
              </template>

              <v-slider
                style="overflow: hidden"
                v-model="volumen"
                color="red"
                background-color="#5f5f5f"
                track-color="#fff"
                vertical
              ></v-slider>
            </v-menu>

            <v-list-item-icon>
              <v-btn icon @click="anterior">
                <v-icon>mdi-rewind</v-icon>
              </v-btn>
            </v-list-item-icon>

            <v-list-item-icon :class="{ 'mx-5': $vuetify.breakpoint.mdAndUp }">
              <v-btn v-show="playPause" @click="pausarSonido" icon>
                <v-icon>mdi-pause</v-icon>
              </v-btn>

              <v-btn v-show="!playPause" @click="reproducirSonido" icon>
                <v-icon>mdi-play</v-icon>
              </v-btn>
            </v-list-item-icon>

            <v-list-item-icon
              class="ml-0"
              :class="{ 'mr-3': $vuetify.breakpoint.mdAndUp }"
            >
              <v-btn icon @click="siguiente">
                <v-icon>mdi-fast-forward</v-icon>
              </v-btn>
            </v-list-item-icon>
          </v-list-item>
        </v-list>
      </v-card>
    </v-bottom-sheet>
    <audio id="cancion">
      <source
        :volume="volumen / 100"
        :src="require(`../assets/music/Lobo hombre en París.mp3`)"
        type="audio/mpeg"
      />
    </audio>
  </div>
</template>

<script>
export default {
  data: () => ({
    //
    volumen: 50,
    sonido: "",
    playPause: false,
    duracion: 0,
    tiempoActual: 0,
    tiempoActualMinutos: "00:00",
    minutos: "00:00",
    apuntador: 1,

    //Cancion
    nombreCancion: "Lobo hombre en París",
    artistaCancion: "La unión",
    srcCancion: "Lobo hombre en París.mp3",

    playlist: [
      {
        title: "Y serás canción",
        artist: "Big Simon",
        srcCancion: "Y serás canción.mp3",
      },
      {
        title: "Lobo hombre en París",
        artist: "La unión",
        srcCancion: "Lobo hombre en París.mp3",
      },
      {
        title: "Hasta que el cuerpo aguante",
        artist: "Mägo de Oz",
        srcCancion: "Hasta que el cuerpo aguante.mp3",
      },
         {
        title: "Ángel",
        artist: "Elefante",
        srcCancion: "Ángel.webm",
      },
      {
        title: "El sueño de un loco",
        artist: "Zenobia",
        srcCancion: "El sueño de un loco.mp3",
      },
      {
        title: "Noche de halloween",
        artist: "Saurom",
        srcCancion: "Noche de halloween.mp3",
      },
    ],
  }),
  props: {
    reproductor: {
      reproductor: Boolean,
      default: true,
    },
  },
  mounted() {
    //
  },
  watch: {
    reproductor(value) {
      if (value) {
        //console.log(this.playlist.length);
        this.duracion = document.getElementById("cancion").duration;
        this.minutos = this.convertir(
          document.getElementById("cancion").duration
        );
      }
    },
    playPause(val) {
      if (val) {
        this.avanzarBarra();
      }
    },
    volumen(val) {
      document.getElementById("cancion").volume = val / 100;
    },
  },
  methods: {
    avanzarBarra() {
      this.tiempoActual =
        (document.getElementById("cancion").currentTime * 100) /
        document.getElementById("cancion").duration;
      this.tiempoActualMinutos = this.convertir(
        document.getElementById("cancion").currentTime
      );

      setTimeout(() => {
        if (this.playPause) {
          this.avanzarBarra();
        }
      }, 1000);
    },
    reproducirSonido() {
      this.playPause = true;
      document.getElementById("cancion").play();
    },
    pausarSonido() {
      this.playPause = false;
      document.getElementById("cancion").pause();
    },
    reinicio() {
      document.getElementById("cancion").currentTime = 0;
    },

    siguiente() {
      this.playPause = true;
      if (this.apuntador + 1 < this.playlist.length) {
        this.apuntador++;
        this.nombreCancion = this.playlist[this.apuntador].title;
        this.artistaCancion = this.playlist[this.apuntador].artist;
        document.getElementById("cancion").src = require(`../assets/music/` +
          this.playlist[this.apuntador].srcCancion);
      } else {
        this.nombreCancion = this.playlist[0].title;
        this.artistaCancion = this.playlist[0].artist;
        document.getElementById("cancion").src = require(`../assets/music/` +
          this.playlist[0].srcCancion);
        this.apuntador = 0;
      }
      document.getElementById("cancion").currentTime = 0;

      setTimeout(() => {
        this.duracion = document.getElementById("cancion").duration;
        this.minutos = this.convertir(
          document.getElementById("cancion").duration
        );
        document.getElementById("cancion").play();
      }, 100);
    },

    anterior() {
      this.playPause = true;
      if (this.apuntador - 1 >= 0) {
        this.apuntador--;
        this.nombreCancion = this.playlist[this.apuntador].title;
        this.artistaCancion = this.playlist[this.apuntador].artist;
        document.getElementById("cancion").src = require(`../assets/music/` +
          this.playlist[this.apuntador].srcCancion);
      } else {
        this.nombreCancion = this.playlist[this.playlist.length - 1].title;
        this.artistaCancion = this.playlist[this.playlist.length - 1].artist;
        document.getElementById("cancion").src = require(`../assets/music/` +
          this.playlist[this.playlist.length - 1].srcCancion);
        this.apuntador = this.playlist.length - 1;
      }
      document.getElementById("cancion").currentTime = 0;

      setTimeout(() => {
        this.duracion = document.getElementById("cancion").duration;
        this.minutos = this.convertir(
          document.getElementById("cancion").duration
        );
        document.getElementById("cancion").play();
      }, 100);
    },

    convertir(valor) {
      //
      if (!valor || typeof valor !== "number") return "00:00";
      let min = parseInt(valor / 60),
        sec = parseInt(valor % 60);
      min = min < 10 ? "0" + min : min;
      sec = sec < 10 ? "0" + sec : sec;
      valor = min + ":" + sec;
      return valor;
    },
  },
  computed: {
    //
  },
};
</script>