<template>
  <div>
    <v-app-bar class="hidden-md-and-up" app color="#333333" dark flat>
      <v-btn icon @click.stop="drawer = !drawer">
        <v-icon>mdi-menu</v-icon>
      </v-btn>

      <v-spacer></v-spacer>

      <v-btn icon @click="reproducir()">
        <v-icon>mdi-music</v-icon>
      </v-btn>
    </v-app-bar>

    <v-navigation-drawer class="hidden-md-and-up" v-model="drawer" fixed dark>
      <v-list-item>
        <v-list-item-avatar>
          <v-img src="https://gartic.com.br/imgs/mural/on/onepiece_united/luffy-monk.png"></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title>Carlitos</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list dense rounded>
        <v-list-item v-for="item in items" :key="item.text" link :to="item.link">
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <Reproductor :reproductor="reproductor" />

    <v-app-bar class="hidden-md-and-down" app color="#333333" dark>

      <v-btn to="/" text>
        {{ titulo }}
      </v-btn>

      <v-spacer></v-spacer>
      <!-- <v-text-field v-model="search" label="🔎 Buscar" hide-details></v-text-field> -->

      <v-btn class="mx-2" small v-for="(item, index) in items" :key="index" :to="item.link" text
        @click="changeTitulo(item)">
        <v-icon>{{ item.icon }}</v-icon>
        <span>{{ item.text }}</span>
      </v-btn>

      <v-spacer></v-spacer>

      <v-btn icon @click="reproducir()">
        <v-icon>mdi-music</v-icon>
      </v-btn>

    </v-app-bar>
  </div>
</template>

<script>
import Reproductor from "../components/Reproductor.vue";
export default {
  name: "ToolBar",
  data: () => ({
    //
    search: "",
    reproductor: false,

    drawer: false,
    titulo: "Inicio",

    items: [
      { text: "Inicio", link: "/", icon: "mdi-home" },
      {
        text: "Proyectos",
        link: "/proyects",
        icon: "mdi-briefcase-eye-outline",
      },
      { text: "Curriculum", link: "/curriculum", icon: "mdi-file-outline" },
      { text: "Blog", link: "/blog", icon: "mdi-post" },
      { text: "PcBuilder", link: "/pcbuilder", icon: "mdi-laptop" },
      {
        text: "Error page",
        link: "/noExisto:,)",
        icon: "mdi-alert-octagon-outline",
      },
    ],
  }),
  mounted() {
    setTimeout(() => {
      //console.log(this.$router.currentRoute.name)
      this.titulo = this.$router.currentRoute.name;
    }, 1000);
    /*
      this.titulo = this.$router.currentRoute.name;
      console.log(this.$router.currentRoute.name)
    */
  },
  components: {
    Reproductor,
  },
  methods: {
    changeTitulo(item) {
      //
      this.titulo = item.text;
    },
    reproducir() {
      if (this.reproductor) {
        let miPrimeraPromise = new Promise((resolve) => {
          this.reproductor = false;
          setTimeout(function () {
            resolve(); // ¡Todo salió bien!
          }, 250);
        });

        miPrimeraPromise.then(() => {
          this.reproductor = true;
        });
      } else {
        this.reproductor = true;
      }
    },
  },
};
</script>

<style scoped>
.indice {
  width: 10rem;
}

.itemTitle:hover {
  background-color: darkgrey;
}
</style>