<template>
  <v-app class="app">
    <Preloader v-show="ver" />
    <ToolBar v-show="!ver" />
    <!---->
    <v-main class="colorFondo" v-show="!ver">
      <router-view />
    </v-main>
    <!---->
    <Footer v-show="!ver" />

  </v-app>
</template>

<script>
import ToolBar from "@/components/ToolBar.vue";
import Footer from "./components/Footer.vue";
import Preloader from "./components/Preloader.vue"
export default {
  name: "App",

  data: () => ({
    //
    mostrar: true,
    ver: true,
  }),
  components: {
    Preloader,
    ToolBar,
    Footer,
  },
  mounted() {
    if (Boolean(this.ver)) this.showToggle()
  },
  methods: {
    showToggle() {
      setTimeout(() => {
        this.ver = false
      }, 2000)
    }
  },
};
</script>

<style>
.colorFondo {
  background-color: #5f5f5f;
}

.v-main {
  padding-top: 55px !important;
}

/* width */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: #5f5f5f;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: red;
  border-radius: 3px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b30000;
}
</style>